import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { RouterModule, Routes } from '@angular/router'; 
import { NgZorroAntModule } from 'src/app/ng-zorro-ant.module';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { IonicModule } from '@ionic/angular';
import { TranslateModule } from '@ngx-translate/core';
import { EditSalesReturnComponent } from './edit-sales-return/edit-sales-return.component';
import { DetailComponent } from './detail/detail.component'; 
import { CanLeavePage } from 'src/app/canLeavePage';
import { ScrollingModule } from '@angular/cdk/scrolling'; 
import { SalesReturnRetailInvoiceComponent } from './sales-return-retail-invoice.component';
import { SalesDesktopBillType1Module } from 'src/app/sales/components/bill-type/bill-type-1/sales-desktop/sales-desktop-bill-type1.module';



const routes: Routes = [
  {
    path: '',
    children: [
      {
        path: "",
        component: SalesReturnRetailInvoiceComponent
      },
      {
        path: "detail",
        component: DetailComponent
      },
      {
        path: "edit-sales",
        component: EditSalesReturnComponent,
       
      },
    ]
  },

]
@NgModule({
  declarations: [SalesReturnRetailInvoiceComponent,
    EditSalesReturnComponent, DetailComponent,  ],
  imports: [
    CommonModule,
    RouterModule.forChild(routes),
    NgZorroAntModule,
    TranslateModule,
    IonicModule,
    FormsModule,
    ReactiveFormsModule,
    ScrollingModule,
    SalesDesktopBillType1Module
  ]
})
export class SalesReturnRetailInvoiceModule { }
